<!-- 通配页面 -->
<template>
    <div>
        <pageTop />
            <mavon-editor
                class="md"
                :value="prop.context"
                :subfield = "prop.subfield"
                :defaultOpen = "prop.defaultOpen"
                :toolbarsFlag = "prop.toolbarsFlag"
                :editable="prop.editable"
                :scrollStyle="prop.scrollStyle"
            ></mavon-editor>
        <pageFooter />
    </div>
</template>
<script>
export default {
    name: 'about',
    data() {
        return {
            id:'',
            param:'',
            prop: {
                subfield: false,// 单双栏模式
                defaultOpen: 'preview',//edit： 默认展示编辑区域 ， preview： 默认展示预览区域 
                editable: false,
                toolbarsFlag: false,
                scrollStyle: false,
                context: '\n\n# 车 \n## 牌\n### 识\n#### 别 \n ##### 工\n > 具   \n'+' \n```这是代码：asdasdas```\n```这是代码：asdasdas```\n```这是代码：asdasdas```'
            }
        }
    },
    created() {
        this.id = this.$route.params.id;
        this.param = this.$route.query.param;
        console.info(this.id);
        console.info(this.param);

        this.$Spin.show();
        setTimeout(() => {
            this.$Spin.hide();
        }, 3000);
    },
    methods: {
        
        
    }
}
</script>



